body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#home {
  display: grid;
  grid-template-columns: 15% 1fr 1fr 15%;
  grid-template-rows: 359px 0px 300px 300px;
}

#hero {
  grid-column: 1 / 5;
  grid-row: 1 / 2;
  background-color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#gathering-info {
  grid-column: 1 / 3;
  grid-row: 3 / 4;
  background-color: hsla(0, 0%, 100%, 0.93);
  -webkit-transition-duration: 0.7s;
          transition-duration: 0.7s;
}

#mission-info {
  grid-column: 3 / 5;
  grid-row: 3 / 4;
  background-color: hsla(0, 0%, 100%, 0.93);
  color: #000;
  border-left: 1px solid #333;
  -webkit-transition-duration: 0.7s;
          transition-duration: 0.7s;
}

#gathering-info:hover,
#mission-info:hover {
  background-color: #fff;
}

#giving-info {
  grid-column: 2 / 4;
  grid-row: 4 /5;
  background-color: hsl(163, 41%, 45%, 0.75);
  color: #fff;
  border: 1px solid #000;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  margin-bottom: 20px;
  box-shadow: 12px 12px 10px -8px rgba(0, 0, 0, 0.74);
}

#giving-info:hover {
  background-color: hsl(163, 41%, 45%);
  margin-top: 2px;
  margin-bottom: 18px;
  box-shadow: 20px 20px 10px -8px rgba(0, 0, 0, 0.64);
}

.info {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.info h3 {
  margin: 0 auto;
}

.info a {
  margin: auto;
}
