.sidebar-links {
  padding: 10px 0;
}

.sidebar-links ul {
  margin: 20vh 0;
  padding: 1px 2.8px;
  width: 100%;
  background-color: #fff;
}

.sidebar-links ul li {
  display: block;
  padding: 10px 10px;
}

.sidebar-links a {
  color: #000;
  text-decoration: none;
}

.sidebar-links a:hover,
.sidebar-links a:focus {
  text-decoration: underline;
}

.main {
  display: inline-block;
}

#connect a {
  color: #000;
  border-right: 1px solid #000;
  padding: 5px 5px 0 5px;
  -webkit-transition-duration: 0.7s;
          transition-duration: 0.7s;
}

#connect a:visited {
  color: #000;
}

#connect a:hover,
#connect a:focus {
  color: #fff;
}

#statementoffaith {
  display: -webkit-flex;
  display: flex;
  background-color: #f0f0f0;
}

#statement {
  margin-left: auto;
  margin-right: auto;
}

body {
  box-sizing: border-box;
  margin: 0;
  background-color: #93cbab;
}

nav {
  grid-area: navbar;
  background-color: #44a187;
  color: #fff;
  margin: 0;
  padding: 10px;
}

.MuiButton-label-2 {
  color: #fff;
}

main {
  display: grid;
  grid-template-columns: 150px auto;
  grid-template-rows: auto;
  grid-template-areas: "sidebar main";
}

ul li {
  display: inline;
  padding: 0 10px;
}

.main {
  grid-area: main;
  padding: 10px;
}

.sidebar {
  grid-area: sidebar;
  position: fixed;
}

